import router from '@/router/index'
export default{
    data(){
        return{
        	person:{
        		name:"",
        		mobile:"",
        		email:"",
        		street:"",
        		qq:""  		
        	}
        }
    },
    methods:{
    	
		saveAddr(){
			var url = _baseUrl +"/mobile/oa/personalAddrBook/save.do";
			this.$ajax({
				url:url,
				method:'POST',
				params:{
					data:this.person
				}   			
			}).then(response => {
				
		        this.$dialog.toast({
		            mes: '保存成功',
		            timeout: 1500,
		            icon: 'success',
		            callback:router.go(-1)
		        });
	    	}).catch(error=> {
				   	console.log(error);
				});
			},
		cancel(){
   			router.go(-1);
   		}
    }
}



